/* For desktops in landscape (e.g., screens wider than 1024px) */
@media only screen and (min-width: 1025px) and (orientation: landscape) {
  .block-team {
    width: 100%;
    // margin: 0 auto;

    display: flex;
    justify-content: center;
    align-items: center;

    border-bottom: 2px solid rgb(128, 128, 128);

    font-size: 11px;
    font-weight: 700;
  }
  .block-team-position,
  .block-team-name,
  .block-team-value,
  .block-team-rating,
  .block-team-goal-per-game,
  .block-team-missed-goal-per-game,
  .block-team-won-duels-procent,
  .block-team-big-chances,
  .block-team-big-chances-created,
  .block-team-big-chances-missed,
  .block-team-shots-on-target,
  .block-team-accurateOwnHalfPassesPercentage,
  .block-team-accurateOppositionHalfPassesPercentage,
  .block-team-avgAge,
  .block-team-averageBallPossession,
  .block-team-accuratePassesPercentage {
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 16px;
  }
  .block-team-position {
    width: 30px;
    height: 30px;
    border-width: 4px;
    border-style: solid;
    border-radius: 90%;
  }
  .block-team-name {
    width: 150px;
    font-size: 18px;
    font-weight: 700;
    text-align: center;
  }
  .block-team-rating {
    border-radius: 5px;
    font-size: 18px;
    font-weight: 700;
  }

  .block-team-logo,
  .block-team-logo-img {
    width: 50px;
    height: 50px;
    margin: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .header-block,
  .header-block-logo {
    // font-size: 8px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    //------------------------
    // writing-mode: vertical-lr; /* horizontal-tb | vertical-rl | vertical-lr;*/
    // text-orientation: mixed;
    word-break: break-word;
    text-align: center;
    padding: 3px;
  }

  .explanation-block {
    width: 100%;

    margin: 0 auto;
    font-style: italic;
    font-size: 13px;
    font-weight: 100;
    // display: flex;
    // justify-content: center;
    align-items: center;
  }
}
