/* For small devices in landscape (e.g., phones) */
@media only screen and (max-width: 600px) and (orientation: landscape) {
  .block-team {
    width: 100%;
    // margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 5px solid rgb(128, 128, 128);
    font-size: 12px;
    font-weight: 700;
  }
  .block-team-position,
  .block-team-name,
  .block-team-value,
  .block-team-rating,
  .block-team-goal-per-game,
  .block-team-missed-goal-per-game,
  .block-team-won-duels-procent,
  .block-team-big-chances,
  .block-team-big-chances-created,
  .block-team-big-chances-missed,
  .block-team-shots-on-target,
  .block-team-accurateOwnHalfPassesPercentage,
  .block-team-accurateOppositionHalfPassesPercentage,
  .block-team-avgAge,
  .block-team-averageBallPossession,
  .block-team-accuratePassesPercentage {
    width: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
  }
  .block-team-position {
    width: 20px;
    height: 20px;
    border-width: 2px;
    border-style: solid;
    border-radius: 90%;
  }
  .block-team-name {
    width: 90px;
    font-size: 13px;
    font-weight: 700;
  }
  .block-team-big-chances,
  .block-team-big-chances-created,
  .block-team-big-chances-missed,
  .block-team-shots-on-target {
    width: 35px;
  }
  .block-team-value {
    width: 70px;
    font-size: 12px;
  }
  .block-team-rating {
    border-radius: 5px;
    font-size: 12px;
    font-weight: 700;
  }

  .block-team-logo,
  .block-team-logo-img {
    width: 35px;
    height: 35px;
    margin: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .header-block,
  .header-block-logo {
    height: 20px;
    font-weight: 700;
    display: flex;
    justify-content: center;
    align-items: center;
    word-break: break-word;
    text-align: center;
    padding: 2px;
    font-size: 8px;
  }

  .explanation-block {
    width: 100%;
    margin: 0 auto;
    font-size: 10px;
    font-weight: 100;
    align-items: center;
    font-style: italic;
  }
}
